.job-listings-container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 20px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
}

/* Upload Section */
.upload-section {
  margin-bottom: 20px;
}

.upload-button {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 12px 24px;
  background: #0a66c2;
  color: white;
  border-radius: 24px;
  cursor: pointer;
  font-weight: 600;
  font-size: 14px;
  transition: background 0.2s;
}

.upload-button:hover {
  background: #004182;
}

/* Filter Chips */
.filter-chips-container {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-bottom: 20px;
  padding: 16px;
  background: #f3f6f8;
  border-radius: 8px;
}

.filter-chip {
  padding: 8px 16px;
  background: white;
  border: 1px solid #d0d5dd;
  border-radius: 20px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  transition: all 0.2s;
  user-select: none;
}

.filter-chip:hover {
  border-color: #0a66c2;
  background: #f0f7ff;
}

.filter-chip.active {
  background: #0a66c2;
  color: white;
  border-color: #0a66c2;
}

/* Panels Container */
.panels-container {
  display: grid;
  grid-template-columns: 400px 1fr;
  gap: 20px;
  min-height: 600px;
}

/* Left Panel */
.left-panel {
  border: 1px solid #d0d5dd;
  border-radius: 8px;
  background: white;
  overflow-y: auto;
  max-height: 800px;
}

.job-count {
  padding: 16px;
  font-size: 14px;
  color: #666;
  border-bottom: 1px solid #e5e5e5;
  font-weight: 500;
}

.job-item {
  padding: 16px;
  border-bottom: 1px solid #e5e5e5;
  cursor: pointer;
  transition: background 0.2s;
}

.job-item:hover {
  background: #f3f6f8;
}

.job-item.selected {
  background: #eef3f8;
  border-left: 3px solid #0a66c2;
}

.job-item-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 12px;
  margin-bottom: 8px;
}

.job-title {
  font-size: 16px;
  font-weight: 600;
  color: #000;
  margin: 0;
  line-height: 1.3;
  flex: 1;
}

.job-badges {
  display: flex;
  gap: 4px;
  flex-shrink: 0;
}

.badge {
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 11px;
  font-weight: 600;
  text-transform: uppercase;
  white-space: nowrap;
}

.badge.easy-apply {
  background: #e7f3e7;
  color: #057642;
}

.badge.applied {
  background: #e0e7ff;
  color: #3538cd;
}

.company-name {
  font-size: 14px;
  color: #666;
  margin-bottom: 6px;
}

.job-meta {
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 13px;
  color: #666;
}

.separator {
  color: #d0d5dd;
}

.no-jobs {
  padding: 60px 20px;
  text-align: center;
  color: #666;
}

.no-jobs p {
  margin: 8px 0;
}

.no-jobs .hint {
  font-size: 14px;
  color: #999;
}

/* Right Panel */
.right-panel {
  border: 1px solid #d0d5dd;
  border-radius: 8px;
  background: white;
  overflow-y: auto;
  max-height: 800px;
}

.no-selection {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 400px;
  color: #999;
  gap: 16px;
}

.job-details {
  padding: 24px;
}

.details-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 16px;
  margin-bottom: 12px;
}

.details-header h2 {
  font-size: 24px;
  font-weight: 600;
  color: #000;
  margin: 0;
  line-height: 1.3;
}

.details-badges {
  display: flex;
  gap: 8px;
  flex-shrink: 0;
}

.details-company {
  font-size: 18px;
  color: #666;
  margin-bottom: 12px;
  font-weight: 500;
}

.details-meta {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
  color: #666;
  margin-bottom: 24px;
}

/* Keyword Tags */
.keyword-tags {
  display: flex;
  gap: 8px;
  margin-bottom: 16px;
}

.keyword-tag {
  padding: 6px 12px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.keyword-tag.b2b {
  background: #e0f2fe;
  color: #0369a1;
  border: 1px solid #7dd3fc;
}

.keyword-tag.saas {
  background: #f0fdf4;
  color: #15803d;
  border: 1px solid #86efac;
}

.keyword-tag.b2c {
  background: #fef3c7;
  color: #a16207;
  border: 1px solid #fde047;
}

.details-actions {
  display: flex;
  gap: 12px;
  margin-bottom: 32px;
  flex-wrap: wrap;
}

.external-app-container {
  display: flex;
  align-items: center;
  gap: 12px;
}

.clicked-date {
  font-size: 13px;
  color: #15803d;
  font-weight: 500;
  padding: 8px 12px;
  background: #f0fdf4;
  border-radius: 6px;
  border: 1px solid #bbf7d0;
  white-space: nowrap;
}

.action-button {
  padding: 12px 24px;
  border-radius: 24px;
  font-weight: 600;
  font-size: 14px;
  text-decoration: none;
  text-align: center;
  transition: all 0.2s;
  display: inline-block;
}

.action-button.primary {
  background: #0a66c2;
  color: white;
}

.action-button.primary:hover {
  background: #004182;
}

.action-button.secondary {
  background: white;
  color: #0a66c2;
  border: 1px solid #0a66c2;
}

.action-button.secondary:hover {
  background: #f0f7ff;
}

/* Hiring Team */
.hiring-team {
  margin-bottom: 32px;
}

.hiring-team h3 {
  font-size: 18px;
  font-weight: 600;
  margin: 0 0 16px 0;
  color: #000;
}

.team-members {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
}

.team-member {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px 16px;
  background: #f3f6f8;
  border-radius: 8px;
  text-decoration: none;
  color: #000;
  transition: background 0.2s;
}

.team-member:hover {
  background: #e5e8eb;
}

.member-avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #0a66c2;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  font-size: 16px;
}

.member-name {
  font-weight: 500;
  font-size: 14px;
}

/* Applicant Stats */
.applicant-stats {
  margin-bottom: 32px;
}

.applicant-stats h3 {
  font-size: 18px;
  font-weight: 600;
  margin: 0 0 16px 0;
  color: #000;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 16px;
}

.stat-item {
  padding: 16px;
  background: #f3f6f8;
  border-radius: 8px;
  text-align: center;
}

.stat-value {
  font-size: 28px;
  font-weight: 600;
  color: #0a66c2;
  margin-bottom: 4px;
}

.stat-label {
  font-size: 13px;
  color: #666;
}

/* Job Description */
.job-description {
  margin-top: 32px;
  padding-top: 32px;
  border-top: 1px solid #e5e5e5;
}

.job-description h3 {
  font-size: 18px;
  font-weight: 600;
  margin: 0 0 16px 0;
  color: #000;
}

.job-description p {
  line-height: 1.6;
  color: #333;
  margin: 12px 0;
  font-size: 16px;
}

.job-description div {
  font-size: 16px;
  line-height: 1.6;
  color: #333;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .panels-container {
    grid-template-columns: 1fr;
  }

  .left-panel,
  .right-panel {
    max-height: none;
  }
}

/* Scrollbar Styling */
.left-panel::-webkit-scrollbar,
.right-panel::-webkit-scrollbar {
  width: 8px;
}

.left-panel::-webkit-scrollbar-track,
.right-panel::-webkit-scrollbar-track {
  background: #f1f1f1;
}

.left-panel::-webkit-scrollbar-thumb,
.right-panel::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 4px;
}

.left-panel::-webkit-scrollbar-thumb:hover,
.right-panel::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}